import { FETCH_USER, FETCH_USER_FAILED, FETCH_USER_SUCCESS, HIDE_MODAL, LOGIN_MODAL, REGISTER_MODAL } from './constant'

import fetchAvatarService from '../services/services'

export default avatar => {
  return dispatch => {
    dispatch(FETCH_USER())
    fetchAvatarService(avatar)
      .then(item => dispatch(FETCH_USER_SUCCESS(item)))
      .catch(error => dispatch(FETCH_USER_FAILED(error)))
  }
}

export const loginModal = () => {
  return {
    type: LOGIN_MODAL
  }
}

export const registerModal = () => {
  return {
    type: REGISTER_MODAL
  }
}

export const hideModal = () => {
  return {
    type: HIDE_MODAL
  }
}

export const fetchUser = () => ({
  type: FETCH_USER
})

export const fetchUserSuccess = user => ({
  type: FETCH_USER_SUCCESS,
  payload: {
    user
  }
})

export const fetchUserFailed = error => ({
  type: FETCH_USER_FAILED,
  payload: { error }
})
