import React, { useRef } from 'react'
import { Link } from 'react-router-dom'
import styles from '../PostDetailModal.module.scss'
import userImage from '../../../assets/img/user.jpg'
import { AiFillHeart } from 'react-icons/ai'
const defaultFn = () => {}
const Comment = ({ commentPost, onDeleteComment = defaultFn }) => {
  const imageRef = useRef(null)

  const handleErrorImage = () => {
    imageRef.current.src = userImage
  }

  return (
    <div className={styles.commentItem}>
      <div className={styles.commentContent}>
        <Link to={`/@${commentPost.user.nickname}`}>
          <img
            onError={handleErrorImage}
            ref={imageRef}
            src={commentPost.user.avatar}
            alt="avatar"
            className={styles.avatar}
          />
        </Link>
        <div className={styles.commentWrap}>
          <Link className={styles.nicknameCmt} to={`/@${commentPost.user.nickname}`}>
            {commentPost.user.nickname}
          </Link>
          <div className={styles.commentText}>
            <span className={styles.cmt}>{commentPost.comment}</span>
            <div className={styles.bottomContainer}>
              <span className={styles.commentTime}>1 giy trc</span>
              <span onClick={() => onDeleteComment(commentPost)} className={styles.deleteComment}>
                Xoa
              </span>
            </div>
          </div>
        </div>
        <div className={styles.likeContainer}>
          <AiFillHeart className={styles.icon} />
          <span className={styles.likeCounts}>{commentPost.likes_count}</span>
        </div>
      </div>
    </div>
  )
}

export default Comment
