import React, { useRef, useState, useEffect } from 'react'
import styles from './PostItem.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPause, faPlay, faVolumeMute, faVolumeUp } from '@fortawesome/free-solid-svg-icons'
import { Link } from 'react-router-dom'
import config from '../../config'
import Button from '../common/Button'
import { AiFillHeart } from 'react-icons/ai'
import { FaCommentDots, FaShare } from 'react-icons/fa'
import Modal from 'react-modal'
import Login from '../Login'
import Register from '../../components/Register'
import { useDispatch, useSelector } from 'react-redux'
import { hideModal, loginModal, registerModal } from '../../redux/actions'
import { Waypoint } from 'react-waypoint'
import userImage from '../../assets/img/user.jpg'

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)'
  }
}
const token = window.localStorage.getItem('token')
const LOGIN_MODAL = 'LOGIN_MODAL'
const REGISTER_MODAL = 'REGISTER_MODAL'
const defaultFn = () => {}
const PostItem = ({
  data = '',
  hastag = '',
  isMuted = false,
  isPlaying = false,
  stopWhenPaused = false,
  isWaypoint = false,
  onShowDetail = defaultFn,
  onFollowUser = defaultFn,
  onToggleMute = defaultFn,
  getVideoRef = defaultFn,
  onTogglePlay = defaultFn,
  onEnterWaypoint = defaultFn,
  onLikeCount = defaultFn,
  onCommentCount = defaultFn
}) => {
  const videoRef = useRef(null)
  const imageRef = useRef(null)
  const modalLogin = useSelector(state => state.modal.modalLogin)
  const modalRegister = useSelector(state => state.modal.modalRegister)
  const dispatch = useDispatch()

  useEffect(() => {
    if (!videoRef.current) return
    if (isPlaying) {
      videoRef.current.play()
    } else {
      videoRef.current.pause()
      if (stopWhenPaused) {
        videoRef.current.currentTime = 0
      }
    }
  }, [isPlaying, stopWhenPaused])

  const getVideoWidth = () => {
    const videoWidth = data.meta.video.resolution_x
    const videoHeight = data.meta.video.resolution_y
    const videoRatio = videoWidth / videoHeight
    if (videoWidth > videoHeight) {
      return `calc(400px + ((100vw - 768px) / 1152) * 100)`
    }
    return `calc(${videoRatio} * (400px + ((100vw - 768px) / 1152) * 100))`
  }

  const topOffsetVideo = () => {
    return (window.innerHeight - 60) / 3
  }

  const bottomOffsetVideo = () => {
    return (window.innerHeight - 60) / 3
  }

  const handleErrorImage = () => {
    imageRef.current.src = userImage
  }

  const openModal = () => {
    const action = loginModal()
    dispatch(action)
  }

  const closeModal = () => {
    const action = hideModal()
    dispatch(action)
  }

  const handleShowModalRegister = () => {
    const action = registerModal()
    dispatch(action)
  }

  const handleShowModalLogin = () => {
    const action = loginModal()
    dispatch(action)
  }

  return (
    <div className={styles.postItem}>
      <Link to={`${config.routes.home}@${data.user.first_name} ${data.user.last_name}`} className={styles.userAvatar}>
        <img
          src={data.user.avatar}
          alt={data.user.nickname}
          className={styles.avatarImg}
          onError={handleErrorImage}
          ref={imageRef}
        />
      </Link>
      <div className={styles.postContent}>
        <div className={styles.infoContent}>
          <Link to={`${config.routes.home}@${data.user.first_name} ${data.user.last_name}`} rel="noopener">
            <h3 className={styles.authorUnique}>{data.user.nickname}</h3>
          </Link>
          <Link to={`${config.routes.home}${data.user.first_name} ${data.user.last_name}`} rel="noopener">
            <h4 className={styles.fullName}>{`${data.user.first_name} ${data.user.last_name}`}</h4>
          </Link>
        </div>
        <div className={styles.caption}>
          <Link to={`${config.routes.home}${data.user.first_name} ${data.user.last_name}`} rel="noopener">
            <strong>{hastag}</strong>
          </Link>
          <strong>{data.description}</strong>
        </div>
        {token ? (
          <div className={styles.btnFollow}>
            <Button
              border
              color="colorPrimary"
              size="sm"
              type="default"
              hoverPrimaryColor
              textCenter
              onClick={() => onFollowUser(data)}
              actived={data.user.is_followed}
            >
              <span>{data.user.is_followed ? 'ang follow' : 'Follow'}</span>
            </Button>
          </div>
        ) : (
          <div className={styles.btnFollow}>
            <Button
              border
              color="colorPrimary"
              size="sm"
              type="default"
              hoverPrimaryColor
              textCenter
              onClick={openModal}
            >
              <span>Follow</span>
            </Button>
          </div>
        )}

        <Modal isOpen={modalLogin === LOGIN_MODAL} style={customStyles}>
          <Login
            title="Logout to Tiktok"
            accountTitle="Ban a co tai khoan"
            loginText="ng nhp"
            onShowModalLogin={handleShowModalRegister}
            onCloseModal={closeModal}
          />
        </Modal>
        <Modal isOpen={modalRegister === REGISTER_MODAL} style={customStyles}>
          <Register
            title="Login to Tiktok"
            accountTitle="Ban khng co tai khoan"
            loginText="ng ky"
            onShowModalRegister={handleShowModalLogin}
            onCloseModal={closeModal}
          />
        </Modal>
        <div className={styles.content}>
          <div className={styles.videoItem}>
            <div className={styles.cardWrapper}>
              <div className={styles.cardImage}>
                {isWaypoint && (
                  <Waypoint
                    onEnter={() => onEnterWaypoint(data)}
                    topOffset={topOffsetVideo()}
                    bottomOffset={bottomOffsetVideo()}
                  >
                    <video
                      style={{ width: getVideoWidth() }}
                      className={styles.video}
                      loop
                      muted={isMuted}
                      autoPlay
                      ref={ref => {
                        videoRef.current = ref
                        getVideoRef(ref, data)
                      }}
                      poster={data.thumb_url}
                      src={data.file_url}
                      onClick={() => onShowDetail(data)}
                    />
                  </Waypoint>
                )}
                <span className={styles.overlay} />
                <button className={[styles.btn, styles.playBtn].join(' ')} onClick={() => onTogglePlay(data)}>
                  <FontAwesomeIcon className={styles.playIcon} icon={isPlaying ? faPause : faPlay} />
                </button>
                <button className={[styles.btn, styles.vlBtn].join(' ')} onClick={() => onToggleMute(data)}>
                  <FontAwesomeIcon className={styles.vlIcon} icon={isMuted ? faVolumeMute : faVolumeUp} />
                </button>
              </div>
            </div>
          </div>
          <div className={styles.iconAction}>
            <div className={styles.itemWrapper}>
              <div className={styles.barItem} onClick={() => onLikeCount(data)}>
                <AiFillHeart className={`${styles.icon} ${data.is_liked ? styles.likeIcon : ''}`} />
              </div>
              <strong>{data.likes_count}</strong>
            </div>

            <div className={styles.itemWrapper}>
              <div className={styles.barItem} onClick={() => onCommentCount(data)}>
                <FaCommentDots className={styles.icon} />
              </div>
              <strong>{data.comments_count}</strong>
            </div>

            <div className={styles.itemWrapper}>
              <div className={styles.barItem}>
                <FaShare className={styles.icon} />
              </div>
              <strong>{data.shares_count}</strong>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default PostItem
