import axiosInstance from '../../axiosInstance'
import { fetchUser, fetchUserSuccess, fetchUserFailed } from '../actions'

const fetchUserService = () => {
  return dispatch => {
    dispatch(fetchUser())
    axiosInstance
      .get('/api/auth/me')
      .then(res => {
        const data = res.data
        dispatch(fetchUserSuccess(data))
      })
      .catch(err => {
        dispatch(fetchUserFailed(err))
      })
  }
}
export default fetchUserService
